/**
 * @file legend component
 * @author zengxiaohui(zengxiaohui@baidu.com)
 */

// base
import Component from './component';

// util
import Util from '../common/util';

// d3
import {scaleOrdinal, schemeCategory10} from 'd3-scale';
import {select as d3Select} from 'd3-selection';

/**
 * title component
 *
 * @class TitleComponent
 * @extends {Component}
 */
export default class LegendComponent extends Component {

     /**
     * constructor
     */
    constructor() {
        super();
    }

    /**
     * render component to container
     *
     * options see follows
     *
     * {
     *     show: true,
     *     left: 'center',
     *     top: 'auto',
     *     right: 'auto',
     *     bottom: 'auto',
     *     orient: 'horizontal',
     *     itemGap: 10,
     *     itemWidth: 60,
     *     iconWidth: 14,
     *     iconHeight: 14,
     *     data: [
     *         {name: '仁和', icon: 'rect', textStyle: {}},
     *         {name: '仲景', icon: 'rect', textStyle: {}}
     *     ],
     *     // ['仁和', '仲景']
     *     textStyle: {
     *         color: '#fff',
     *         fontSize: 14,
     *         fontFamily: ''
     *     },
     *     tooltip: {
     *     }
     * }
     *
     * @param  {Selection} container d3 selection
     * @param  {Object} options      component options
     * @return {Selection}           rendered component
     */
    render(container, options) {
        let me = this;

        if (!options.show) {
            return;
        }

        let g = container.append('g').attr('class', 'd3-legend-group');

        let {
            data,
            iconWidth,
            iconHeight,
            itemWidth,
            color,
            orient = 'horizontal'
        } = options;

        let colorScale = scaleOrdinal(color || schemeCategory10);
        let isVertical = orient === 'vertical';

        // 图例项
        let legendItems = g.selectAll('g.d3-legend-item')
            .data(data)
            .enter()
            .append('g')
            .attr('class', 'd3-legend-item')
            .attr('transform', (d, i) => {
                return (isVertical
                    ? `translate(0, ${i * 30})`
                    : `translate(${i * itemWidth}, 0)`);
            })
            .on('click', function (d, i) {
                let item = this;
                let rect = d3Select(this.querySelector('rect'));
                let text = d3Select(this.querySelector('text'));

                me.onclick(d, i, !!item.disabled).then(
                    function () {
                        // 设置 disabled 状态
                        item.disabled = !item.disabled;

                        // 如果未选中，颜色置灰
                        rect.attr('fill', item.disabled ? '#ccc' : colorScale(i));
                        text.attr('fill', item.disabled ? '#ccc' : 'inherit');
                    },
                    function (x) {
                        console.log(x);
                    }
                );
            });


        // 图例图元
        let rects = legendItems.append('rect')
            .attr('width', iconWidth || 14)
            .attr('height', iconHeight || 14)
            .attr('fill', (d, i) => colorScale(i));

        // 图例文本
        let texts = legendItems.append('text')
            .attr('dx', 19)
            .attr('dy', 11)
            .text(d => d.name || d);

        // 计算坐标
        let [left, top] = Util.calculatePosition(g, container, options);
        g.attr('transform', `translate(${left}, ${top + 10})`);

        this.g = g;
        return g;
    }

    /**
     * 点击事件
     *
     * @return  {Promise} 返回 Promise 对象
     */
    onclick() {
        return new Promise(resolve => resolve());
    }

    /**
     * dispose component
     */
    dispose() {
        this.g.remove();
    }
}



