/**
 * @file title component
 * @author zengxiaohui(zengxiaohui@baidu.com)
 */

// base
import Component from './component';

// util
import Util from '../common/util';

/**
 * title component
 *
 * @class TitleComponent
 * @extends {Component}
 */
export default class TitleComponent extends Component {

    /**
     * constructor
     */
    constructor() {
        super();
    }

    /**
     * render component to container
     *
     * options see follows
     *
     * {
     *     show: true,
     *     text: '模拟迁徙',
     *     subtext: '数据纯属虚构',
     *     left: 'center',
     *     top: 'auto',
     *     itemGap: 10,
     *     textStyle: {
     *         color: '#fff',
     *         fontSize: 14,
     *         fontFamily: ''
     *     },
     *     subtextStyle: {
     *         color: '#fff',
     *         fontSize: 14,
     *         fontFamily: ''
     *     }
     * }
     *
     * @param  {Selection} container d3 selection
     * @param  {Object} options      component options
     * @return {Selection}           rendered component
     */
    render(container, options) {

        if (!options.show) {
            return;
        }

        let g = container.append('g').attr('class', 'title-component');
        let fontFamily = 'Microsoft YAHEI';
        let {textStyle = {}, subtextStyle = {}} = options;

        // 主标题字体
        let fontSize = 14;
        if (textStyle.fontSize) {
            fontSize = +((textStyle.fontSize + '').replace('px', ''));
        }

        if (options.text) {
            g.append('text')
                .text(options.text)
                .attr('fill', textStyle.color || '#666')
                .style('font-family', textStyle.fontFamily || fontFamily)
                .style('font-size', fontSize + 'px');
        }

        // 副标题字体
        let subFontSize = 12;
        if (subtextStyle.fontSize) {
            subFontSize = +subtextStyle.fontSize.replace('px', '');
        }

        if (options.subtext) {
            g.append('text')
                .text(options.subtext)
                .attr('y', fontSize + (options.itemGap || 0))
                .attr('fill', subtextStyle.color || '#999')
                .style('font-family', subtextStyle.fontFamily || fontFamily)
                .style('font-size', (subFontSize ? subFontSize : (fontSize - 2)) + 'px');
        }

        let [left, top] = Util.calculatePosition(g, container, options);
        g.attr('transform', `translate(${left}, ${top + 10})`);

        return g;
    }

    /**
     * dispose component
     */
    dispose() {

    }
}



