/**
 * @file Chord, see http://circos.ca/intro/tabular_visualization/
 * @author zengxiaohui(zengxiaohui@baidu.com)
 */

// css
import '../static/css/chord.less';

// lodash
import _defaultsDeep from 'lodash-es/defaultsDeep';

// d3
import {descending as d3Descending} from 'd3-array';
import {select as d3Select, event as d3Event} from 'd3-selection';
import {arc as d3Arc} from 'd3-shape';
import {rgb as d3Rgb} from 'd3-color';
import {scaleOrdinal as d3ScaleOrdinal, schemeCategory10} from 'd3-scale';
import {
    chord as d3Chord,
    ribbon as d3Ribbon
} from 'd3-chord';
import 'd3-transition';

// chart components
import Legend from '../component/legend';

// util
import Util from '../common/util';

// chart base
import Chart from './chart';

// default options
const defaultOptions = {
    el: 'body',
    color: schemeCategory10
};

/**
 * Chord
 *
 * @class Chord
 * @extends {Chart}
 */
export default class Chord extends Chart {

    /**
     * get chart type
     *
     * @return {string} chart type
     */
    static type() {
        return 'Chord';
    }

    /**
     * constructor
     *
     * @param  {Object} options options
     */
    constructor(options) {
        super();

        this.options = _defaultsDeep(options, defaultOptions);
        this.initOption();
        this.render();
    }

    /**
     * 根据参数，创建一些全局变量
     */
    initOption() {
        let legend = this.options.legend;
        let legendData = legend.data;

        // 标签选中数组，选中为1，未选中为0，默认全部选中, [1, 1, 1, 1, ..., 1]
        this.selectedLabelArray = legendData.map(() => 1);

        // svg元素
        this.svg = this.createSVG();

        // 创建一个g图层元素，移动至画面中心，后续元素都会在这里边绘制
        this.rootGroup = this.createRootGroup();

        // 根据标签选中数组，构建颜色序数比例尺
        this.colorScale = this.createColorScale(this.selectedLabelArray);

        // 根据标签选中数组，构建图例标签比例尺
        this.legendScale = this.createLegendScale(this.selectedLabelArray);
    }

    /**
     * 创建 SVG 标签
     *
     * 1. 如果 option 中包含 el 元素，则挂在 el 元素下边，否则挂在 body 元素下
     * 2. 指定 svg 的宽度和高度
     * 3. 指定 svg 样式为 'd3-svg'
     *
     * @return {Object} d3 生成的 svg 元素
     */
    createSVG() {
        let {width, height, el = 'body'} = this.options;
        return Util.createSVG(el, width, height).attr('class', 'd3-svg');
    }

    /**
     * 创建主绘图区域，不包括 legend 区域
     *
     * 1. 由于和弦图属于一个圆形布局，因此需要将原点移动到画面中心
     * 2. 指定 g 的样式为 'd3-root-group'
     *
     * @return {Object} d3 生成的 g 元素
     */
    createRootGroup() {
        let svg = this.svg;
        let {width, height} = this.options;

        // 如果 legend 组件在上部区域，需要腾出一定空间来给 legend 组件
        let paddingTop = 15;
        let left = width / 2;
        let top = height / 2 + paddingTop;

        let g = svg
            .append('g')
            .attr('class', 'd3-root-group')
            .attr('transform', `translate(${left} , ${top})`);

        return g;
    }

    /**
     * 创建颜色比例尺
     *
     * PS: 需要注意，颜色比例尺依赖于 legend 组件的点击事件，需要根据 legend 组件的选中情况动态更新
     *
     * @param {Array.<number>} selectedLabelArray 标签选中数组
     * @return {Function} 颜色序数比例尺
     */
    createColorScale(selectedLabelArray) {
        let color = this.options.color;
        let selectedColor = color.filter((item, i) => selectedLabelArray[i]);
        return d3ScaleOrdinal(selectedColor);
    }

    /**
     * 创建标签比例尺
     *
     * PS: 标签比例尺也依赖于 legend 选中数组(legend点击事件，初始化参数等会影响该数组)
     *
     * @param {Array.<number>} selectedLabelArray 标签选中数组
     * @return {Function} 标签序数比例尺
     */
    createLegendScale(selectedLabelArray) {
        let legendData = this.options.legend.data;
        let selectedLegendData = legendData.filter((item, i) => selectedLabelArray[i]);
        let labels = selectedLegendData.map(item => (item.name || item));

        return d3ScaleOrdinal(labels);
    }

    /**
     * 设置参数，刷新 legend 和主图形
     *
     * @param {Object} options 参数
     * @override
     */
    setOption(options) {

        // 如果SVG标签已经存在，先清空内容
        this.svg && this.svg.html('');

        this.options = _defaultsDeep(options, defaultOptions);
        this.render();
    }

    /**
     * 渲染函数，分别渲染 legend 和主图形
     */
    render() {
        this.renderLegend();
        this.renderChord();
    }

    /**
     * 渲染 legend
     */
    renderLegend() {
        let legendOptions = this.options.legend;
        legendOptions.color = this.options.color;

        // dispose current legend instance
        this.legendInstance && this.legendInstance.dispose();

        let legendInstance = new Legend();
        legendInstance.render(this.svg, this.options.legend);
        legendInstance.onclick = this.onLegendClick.bind(this);

        // record current legend instance
        this.legendInstance = legendInstance;
    }

    /**
     * 点击 legend 事件
     *
     * @param  {Object} d         当前 item 数据
     * @param  {number} i         当前 item 索引
     * @param  {boolean} disabled 当前 item 激活状态
     * @return {Promise}          如果小于两个 reject，否则 resolve
     */
    onLegendClick(d, i, disabled) {
        let me = this;

        return new Promise((resolve, reject) => {

            // 更新选中的标签数组
            me.selectedLabelArray[i] = +disabled;

            let len = me.selectedLabelArray.filter(item => item === 1);
            if (len < 2) {
                me.selectedLabelArray[i] = +(!disabled);
                reject('至少需要选择两个对象');
            }

            // 根据选中的标签，重新计算矩阵等数据
            me.computeMatrix(me.selectedLabelArray);
            resolve();
        });
    }

    /**
     * 根据选择的标签重新计算数据
     *
     * @param  {Array.<number>} labelArray 标签选择数组
     * @return {Array}                     计算完毕后的新关系矩阵数据
     */
    computeMatrix(labelArray) {

        let serieData = this.options.series[0].data;
        let rows = serieData.filter((item, i) => labelArray[i]);
        let result = [];

        rows.forEach(item => {
            let newRow = item.filter((innerItem, j) => labelArray[j]);
            result.push(newRow);
        });

        // 重新计算图例标签比例尺
        this.legendScale = this.createLegendScale(labelArray);

        // 重新颜色比例尺
        this.colorScale = this.createColorScale(labelArray);

        // 重新渲染余弦图(legend 标注不变)
        this.renderChord(result);

        // 返回计算结果
        return result;
    }

    /**
     * 根据关系矩阵渲染余弦图
     *
     * @param  {Array} matrix 关系矩阵数据
     */
    renderChord(matrix) {
        let me = this;
        let rootGroup = this.rootGroup;
        rootGroup.html('');

        // 移除 tooltip
        if (this.tooltip) {
            this.tooltip.node().parentNode.removeChild(this.tooltip.node());
        }

        let {width, height, series} = this.options;
        let ringWidth = this.options.series[0].ringWidth;
        let padding = 30;

        // 计算外圆和内圆半径
        let outerRadius = Math.min(width, height) / 2 - padding;
        let innerRadius = outerRadius - ringWidth;

        let serieData = series[0];

        // 创建和弦图布局
        let chord = d3Chord()

            // 外层节点之间的角度间隔，这里是 6℃
            .padAngle(serieData.padAngle)

            // 某个节点流出线条大小排序，这里降序排列
            .sortSubgroups(d3Descending);

        rootGroup.datum(chord(matrix || serieData.data));

        // 创建曲线
        let arc = d3Arc()
            .innerRadius(innerRadius)
            .outerRadius(outerRadius);

        // 创建外层数据节点
        let groups = rootGroup.append('g')
            .attr('class', 'groups')
            .selectAll('g')
            .data(chords => chords.groups)
            .enter()
            .append('g')
            .on('mouseover', function (d, i) {
                me.mouseover.call(me, d, i, this);
            })
            .on('mousemove', function (d, i) {
                me.mousemove.call(me, d, i, this);
            })
            .on('mouseout', function (d, i) {
                me.mouseout.call(me, d, i, this);
            });

        // 实际创建
        groups.append('path')
            .style('fill', d => this.colorScale(d.index))
            .style('stroke', d => d3Rgb(this.colorScale(d.index)).darker())
            .attr('d', arc);

        // 创建丝带
        let ribbon = d3Ribbon().radius(innerRadius);

        rootGroup.append('g')
            .attr('class', 'ribbons')
            .selectAll('path')
            .data(chords => chords)
            .enter()
            .append('path')
            .attr('d', ribbon)
            .style('fill', d => this.colorScale(d.target.index))
            .style('stroke', d => d3Rgb(this.colorScale(d.target.index)).darker())
            .style('opacity', .5)
            .on('mouseover', function (d, i) {
                me.ribbonMouseover.call(me, d, i, this);
            })
            .on('mousemove', function (d, i) {
                me.ribbonMousemove.call(me, d, i, this);
            })
            .on('mouseout', function (d, i) {
                me.ribbonMouseout.call(me, d, i, this);
            });

        let tooltip = d3Select('body').append('div')
            .attr('class', 'd3-tooltip');

        this.tooltip = tooltip;
        this.legend = this.legendScale;
    }

    /**
     * 丝带鼠标悬浮
     *
     * @param  {Event} e    事件
     * @param  {number} i   索引
     * @param  {HTMLElement} dom 元素
     * @return {Selection}     selection
     */
    ribbonMouseover(e, i, dom) {
        let tooltip = this.tooltip;
        let legend = this.legend;

        e.source.name = legend(e.source.index);
        e.target.name = legend(e.target.index);

        let ribbonTooltip = this.options.series[0].ribbonTooltip;
        if (ribbonTooltip.show) {

            let tip = ribbonTooltip.formatter ? ribbonTooltip.formatter(e) : e.source.value;
            tooltip.html(tip);

            d3Select(dom).style('opacity', .7);
            return tooltip.transition().duration(50).style('opacity', .9);
        }
    }

    /**
     * 丝带鼠标移动
     *
     * @return {Selection}     selection
     */
    ribbonMousemove() {
        let tooltip = this.tooltip;
        return tooltip
            .style('top', (d3Event.pageY - 10) + 'px')
            .style('left', (d3Event.pageX + 10) + 'px');
    }

    /**
     * 丝带鼠标移出
     *
     * @param  {Event} e    事件
     * @param  {number} i   索引
     * @param  {HTMLElement} dom 元素
     * @return {Selection} selection
     */
    ribbonMouseout(e, i, dom) {
        let tooltip = this.tooltip;

        d3Select(dom).style('opacity', .5);
        return tooltip.style('opacity', 0);
    }

    /**
     * 外环鼠标悬停
     *
     * @param  {Event} e    事件
     * @param  {number} i   索引
     * @return {Selection}     selection
     */
    mouseover(e, i) {

        let tooltip = this.tooltip;
        let legend = this.legend;
        e.name = legend(i);

        let groupTooltip = this.options.series[0].groupTooltip;
        if (groupTooltip.show) {

            let tip = groupTooltip.formatter ? groupTooltip.formatter(e) : e.value;
            tooltip.html(tip);
            return tooltip.transition().duration(50).style('opacity', .9);
        }
    }

    /**
     * 外环鼠标移动
     *
     * @return {Selection}     selection
     */
    mousemove() {
        return this.tooltip
            .style('top', (d3Event.pageY - 10) + 'px')
            .style('left', (d3Event.pageX + 10) + 'px');
    }

    /**
     * 外环鼠标移出
     *
     * @return {Selection}     selection
     */
    mouseout() {
        return this.tooltip.style('opacity', 0);
    }

    /**
     * dispose
     */
    dispose() {
        if (this.tooltip) {
            let tooltipNode = this.tooltip.node();
            tooltipNode.parentNode && tooltipNode.parentNode.removeChild(tooltipNode);
        }
    }
}



